.reviews__card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 100%;
  padding: 8px;
  background: rgba(232, 242, 255, 1);
}

.review__image {
  width: 56px;
  height: 47px;
}

.review__card-title {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 150%;
  margin: 16px 0 12px;
}

.review__card-paragraph {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  margin: 0 0 16px;
  line-height: 150%;
}

.review__author {
  display: flex;
  margin-top: auto;
  align-items: center;
}

.review__author-avatar {
  margin-right: 17px;
  width: 36px;
  height: 36px;
}

.review__author-name {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  margin: 0;
}

.review__status {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #808d9e;
  margin: 8px 0 0;
}
